import React, { Component } from "react";
import "./userCre.css";
import * as firebase from "firebase";
import Loader from "./loading_spinner";
class userCre extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: "Login",
      loading: false,
      currentUser: "",
      email: "",
      password: "",
    };
    this.login = this.login.bind(this);
    this.EmailHandleChange = this.EmailHandleChange.bind(this);
    this.PassHandleChange = this.PassHandleChange.bind(this);
    this.login = this.login.bind(this);
  }

  createUser(email) {
    const fire = firebase.firestore();
    const user = firebase.auth().currentUser;
    const ref = fire.collection("USERDATA");
    console.log("currrent user: ", user.uid);
    ref.doc(user.uid).set({
      email: email,
    });
  }
  login(e) {
    this.setState({
      status: "loging in..",
      loading: true,
    });
    e.preventDefault();

    firebase
      .auth()
      .signInWithEmailAndPassword(this.state.email, this.state.password)
      .then((u) => {
        this.setState({
          currentUser: this.state.email,
        });
      })
      .catch((error) => {
        console.log("current user: ", this.state.currentUser);
        console.log(error);
      });
  }

  EmailHandleChange(e) {
    this.setState({ email: e.target.value });
  }
  PassHandleChange(e) {
    this.setState({ password: e.target.value });
  }
  poppSignUp(e) {
    e.preventDefault();
  }
  render() {
    return (
      <div>
        <div className="userLog">
          <form className="form">
            <h1>Sign In</h1>
            <div className="InputField">
              <label>Email:</label>
              <input
                value={this.state.email}
                onChange={this.EmailHandleChange}
                type="text"
                name="email"
                placeholder="Enter email here..."
              />
            </div>
            <div className="InputField">
              <label>Password:</label>
              <input
                value={this.state.password}
                onChange={this.PassHandleChange}
                type="password"
                name="pass"
                placeholder="Enter password"
              />
            </div>
            <div className="btn">
              <button type="submit" id="login" onClick={this.login}>
                {this.state.loading ? (
                  <Loader version="Loading_small_black" />
                ) : (
                  " "
                )}
                {this.state.status}
              </button>
            </div>
          </form>
          <center>
            <div className="userNote">
              <p>
                By signing up you are agreeing to all the user terms and
                servives
              </p>
            </div>
          </center>
        </div>
      </div>
    );
  }
}
export default userCre;
